import { Navbar, Nav, Container, ListGroup } from 'react-bootstrap';
import { Link, Outlet } from 'react-router-dom';
import Balance from './Balance';

function Sidebar({changeColour}) {
  return <nav className='navigation bg-secondary'>
  <ListGroup variant='flush'>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/addtransaction"}>Add transaction</Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/history"}>History of transaction</Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/statistic"}>Statistic</Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/options"}>Future transactions</Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/options"}>Options</Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/login"}>Exit</Link>
    </ListGroup.Item>
  </ListGroup>
  <Balance/>
</nav>
}

export default Sidebar