import { useEffect, useState } from "react";
import { Container } from "react-bootstrap";
import "./statistic.scss"
import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';

function Statistic() {
  const [incomeCategories, setIncomeCategories] = useState([]);
  const [expenceCategories, setExpenceCategories] = useState([]);
  const [overalStatistic, setOveralStatistic] = useState([]);
  const [incomelStatistic, setIncomelStatistic] = useState([]);
  const [expenceStatistic, setExpenceStatistic] = useState([]);

  useEffect(() => {
    const get = async () => {
      await fetch('https://dashakol88.pythonanywhere.com/api/transaction/statistic?transaction_start_date=2023-02-01&transaction_end_date=2023-03-04', {
        credentials: "include",
        mode: 'cors'
      }).then(data => data.json()).then(data => {
        setOveralStatistic(data.statistic_data.slice(0, 2))
        setIncomelStatistic(data.statistic_data.slice(2, data.statistic_data.length))
      });
      await fetch('https://dashakol88.pythonanywhere.com/api/categories', {
        credentials: "include",
        mode: 'cors'
       }).then(data => data.json()).then(data => {
        setIncomeCategories(data.data.filter(el => el.category_type === 1));
        setExpenceCategories(data.data.filter(el => el.category_type === 0));
       })
    }
    get()
  }, [])
  ChartJS.register(ArcElement, Tooltip, Legend);
  const data = {
    labels: incomelStatistic.map(el => Object.keys(el)).join().split(','),
    datasets: [
      {
        label: '# of Votes',
        data: incomelStatistic.map(el => Object.values(el)).join().split(','),
        backgroundColor: [
          'rgba(255, 99, 132, 0.2)',
          'rgba(54, 162, 235, 0.2)',
          'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
        ],
        borderColor: [
          'rgba(255, 99, 132, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 159, 64, 1)',
        ],
        borderWidth: 1,
      },
    ],
  };
    
  return<Container className="py-3">
    {/* {JSON.stringify(incomelStatistic)} */}
    Statistic
    <div style={{width: '20rem'}}>
    <Pie data={data} />

    </div>
  </Container>
}

export default Statistic